import {compose, omit, append, defaultTo, merge, not, reject} from 'ramda';
import * as L from 'partial.lenses';
import {rack} from '../../rack/rack';
import { checkForCycles } from '../../util/check-for-cycles';

const initialValue = rack.modules;

const byModuleId = id => md => md.id === id;
const createConnection = (moduleId, key) => ({
  type: 'connection',
  module: moduleId,
  property: key
});

const root = L.prop('rack');
const drawingValuesL = L.prop('dv');
const idL = L.prop('id');
const moduleNameL = L.prop('moduleName');
const inputsL = L.prop('inputs');
const positionL = L.compose(drawingValuesL, L.prop('p'));
const dimensionsL = L.compose(drawingValuesL, L.prop('d'));
const inputPositionsL = L.compose(drawingValuesL, L.prop('inp'));
const outputPositions = L.compose(drawingValuesL, L.prop('oup'));
const textPositionL = L.compose(drawingValuesL, L.prop('tp'));
const inPositionL = L.compose(drawingValuesL, L.prop('ip'));
const outPositionL = L.compose(drawingValuesL, L.prop('op'));

const disconnectModule = moduleId => md => {
  return {
    ...md,
    inputs: reject(input => input.type === 'connection' && input.module === moduleId, md.inputs)
  }
};

export default (state = initialValue, action) => {
  switch (action.type) {
    case 'ADD_MODULE': return L.set(root, append(action.payload, L.get(root, state)), state);

    case 'REMOVE_MODULE': {
      const moduleId = action.payload;
      const withoutModuleL = L.compose(
        root,
        L.filter(compose(not, byModuleId(moduleId)))
      );

      const filteredRack = compose(
        L.modify(L.elems, disconnectModule(moduleId)),
        L.get(withoutModuleL)
      )(state);

      return L.set(root, filteredRack, state);
    }

    case 'CLEAR_MODULES': return L.set(root, [], state);

    case 'SET_RAW_VALUE': {
      const {moduleId, inputKey, value} = action.payload;
      const pathL = L.compose(
        root,
        L.find(byModuleId(moduleId)),
        inputsL,
        L.prop(inputKey)
      );
      return L.set(pathL, {type: 'value', value}, state);
    }

    case 'SET_MODULE_POSITION': {
      const {moduleId, position} = action.payload;
      const pathL = L.compose(
        root,
        L.find(byModuleId(moduleId)),
        positionL
      );
      return L.set(pathL, position, state);
    }

    case 'DISCONNECT_MODULE_INPUT': {
      const {moduleId, key} = action.payload;
      const pathL = L.compose(
        root,
        L.find(byModuleId(moduleId)),
        inputsL
      );
      return L.modify(pathL, omit([key]), state);
    }

    case 'CONNECT_MODULES': {
      const {
        inputModuleId,
        outputModuleId,
        inputKey,
        outputKey
      } = action.payload;

      const pathL = L.compose(
        root,
        L.find(byModuleId(inputModuleId)),
        inputsL,
        L.prop(inputKey)
      );

      const newRack = L.set(pathL, createConnection(outputModuleId, outputKey), state);

      if (checkForCycles(L.get(root, newRack))) {
        alert('Action results in a cycle');
        return state;
      }

      return newRack;
    }

    case 'UPDATE_DRAWING_VALUES': {
      const pathL = L.compose(
        root,
        L.find(byModuleId(action.payload.moduleId)),
        drawingValuesL
      );

      return L.modify(pathL, merge(action.payload.drawingValues), state);
    }

    default: return L.set(root, defaultTo(initialValue, L.get(root, state)), state);
  }
};

export const selectors = {
  rack: L.get(root),
};

export const getInputPositionsRaw = L.get(inputPositionsL);
export const getOutputPositionsRaw = L.get(outputPositions);
export const getInputPositions = compose(Object.entries, getInputPositionsRaw);
export const getOutputPositions = compose(Object.entries, getOutputPositionsRaw);
export const getPosition = L.get(positionL);
export const getDimensions = L.get(dimensionsL);
export const getId = L.get(idL);
export const getModuleName = L.get(moduleNameL);
export const getInputs = L.get(inputsL);
export const getInputsValues = compose(Object.values, L.get(inputsL));
export const getInputsEntries = compose(Object.entries, L.get(inputsL));
export const getTextPosition = L.get(textPositionL);
export const getInPosition = L.get(inPositionL);
export const getOutPosition = L.get(outPositionL);