import { pointInRect } from '../util/math-util';
import {
  getPosition,
  getDimensions,
  getId
} from '../react/reducers/rack';

export const mousedown = ({rack, isInDeleteMode, globalTranslate, removeModule}, clickPosition) => {
  if (isInDeleteMode) {
    rack.some(md => {
      const pos = globalTranslate(getPosition(md));
      if (pointInRect(pos, getDimensions(md), clickPosition)) {
        removeModule(getId(md));
        return true;
      }
    });
  }
};
