import { pointInRect } from '../util/math-util';
import { vSub, vAdd } from 'vec-la-fp';
import {
  getPosition,
  getDimensions,
  getId
} from '../react/reducers/rack';

const findById = id => md => getId(md) === id;

export const mousedown = (props, clickPosition) => {
  const {rack, globalTranslate, gotoDragMode, setDragData, selectedModules} = props;
  return [...rack].reverse().some(md => {
    const pos = globalTranslate(getPosition(md));
    const dim = [getDimensions(md)[0], 30];
    const id = getId(md);
    if (pointInRect(pos, dim, clickPosition)) {
      if (!selectedModules.includes(id)) {
        props.setSelection([id]);
      }
      gotoDragMode();
      setDragData(id, vSub(clickPosition, pos));
      return true;
    }
  });
};

export const mousemove = (props, clickPosition) => {
  const {
    rack,
    isInDragMode,
    globalOffset,
    draggedModuleId,
    dragOffset,
    setModulePosition,
    selectedModules
  } = props;

  if (isInDragMode) {
    const draggedModule = rack.find(findById(draggedModuleId));
    const [x, y] = clickPosition;
    const [gx, gy] = globalOffset;
    const diff = [x - gx, y - gy];

    selectedModules.forEach(mId => {
      const cMd = rack.find(findById(mId));
      const offsetFromDraggedModule = mId !== draggedModuleId
        ? vSub(getPosition(cMd), getPosition(draggedModule))
        : [0,0];

      setModulePosition(mId, vAdd(vSub(diff, dragOffset), offsetFromDraggedModule));
    });
  }
};
