import { pointInRect, pointInCircle } from '../util/math-util';
import { socketRadius } from '../constants';
import {
  getInputPositions,
  getPosition,
  getDimensions,
  getOutputPositions,
  getId
} from '../react/reducers/rack';

export const start = (props, clickPosition) => {
  const {
    rack,
    globalTranslate,
    gotoConnectingInputMode,
    disconnectModuleInput,
    gotoConnectingOutputMode,
    setConnectingData,
  } = props;
  return rack.some(md => {
    const position = getPosition(md);
    const pos = globalTranslate(position);
    const dim = getDimensions(md);
    const id = getId(md);

    if (pointInRect(pos, dim, clickPosition)) {
      const startedConnection = getInputPositions(md).some(([inputKey, {socket}]) => {
        if (pointInCircle(globalTranslate(socket, position), socketRadius, clickPosition)) {
          if (md.inputs[inputKey]) {
            disconnectModuleInput(id, inputKey);
          }
          gotoConnectingInputMode();
          setConnectingData(id, inputKey);
          return true;
        }
      });

      if (startedConnection) return true;

      return getOutputPositions(md).some(([outputKey, {socket}]) => {
        if (pointInCircle(globalTranslate(socket, position), socketRadius, clickPosition)) {
          gotoConnectingOutputMode();
          setConnectingData(id, outputKey);
          return true;
        }
      });
    }
  });
};


export const end = (props, clickPosition) => {
  const {
    rack,
    globalTranslate,
    isInConnectingInputMode,
    isInConnectingOutputMode,
    connectingModuleId,
    connectingKey,
    connectModules,
    gotoEditMode
  } = props;

  if (isInConnectingInputMode) {
    return rack.some(md => {
      const position = getPosition(md);
      const pos = globalTranslate(md.dv.p);
      const dim = getDimensions(md);
      const id = getId(md);

      if (pointInRect(pos, dim, clickPosition)) {
        getOutputPositions(md).forEach(([outputKey, {socket}]) => {
          if (pointInCircle(globalTranslate(socket, position), socketRadius, clickPosition)) {
            connectModules(connectingModuleId, id, connectingKey, outputKey);
            gotoEditMode();
            return true;
          }
        });
      }
    });
  };

  if (isInConnectingOutputMode) {
    return rack.some(md => {
      const position = getPosition(md);
      const pos = globalTranslate(md.dv.p);
      const dim = getDimensions(md);
      const id = getId(md);

      if (pointInRect(pos, dim, clickPosition)) {
        getInputPositions(md).forEach(([inputKey, {socket}]) => {
          if (pointInCircle(globalTranslate(socket, position), socketRadius, clickPosition)) {
            connectModules(id, connectingModuleId, inputKey, connectingKey);
            gotoEditMode();
            return true;
          }
        });
      }
    });
  };
};
