import * as R from 'ramda';
import {vAdd, vAddAll} from 'vec-la-fp';
import {socketRadius} from '../constants';
import { getTagColor } from './module-tag-colors';
import { findModule } from '../modules';
import { drawBezier } from '../util/drawing/draw-bezier';
import { regularRect } from '../util/math-util';
import {
  getId,
  getPosition,
  getInputs,
  getTextPosition,
  getInPosition,
  getOutPosition,
  getDimensions,
  getModuleName,
  getInputPositionsRaw,
  getOutputPositionsRaw
} from '../react/reducers/rack';


const drawOutputSockets = (mc, ctx, md, rack, translateToPosition) => ([key, { text, socket }]) => {
  mc.fill([255, 255, 255, 1]);
  ctx.fillText(key, ...translateToPosition(text));

  const isConnected = rack.some(rMd =>
    Object.values(rMd.inputs).some(input =>
      input.type === 'connection' &&
      input.module === getId(md) &&
      input.property === key
    )
  );

  const color = isConnected ? [255, 255, 255, 0.5] : [0,0,0,0.5];
  mc.fill(color);
  mc.drawEllipse(mc.circle(socketRadius, translateToPosition(socket)));
};

const drawInputSockets = (mc, ctx, md, translateToPosition, inputs) => ([key, { text, socket }]) => {
  const isValue = inputs[key] && inputs[key].type === 'value';
  const isConnection = inputs[key] && inputs[key].type === 'connection';
  const isRequired = R.compose(
    R.prop('required'),
    R.prop(key),
    getInputs,
    findModule,
    getModuleName
  )(md);

  const color = isValue
    ? [0, 0, 255, 0.5]
    : isConnection
      ? [255, 255, 255, 0.5]
      : isRequired
        ? [255,0,0,0.5]
        : [0,0,0,0.5];

  const input = inputs[key];
  const socketText = isValue
    ? `${key}(${input.value})`
    : key;

  mc.fill([255, 255, 255, 1]);
  ctx.fillText(socketText, ...translateToPosition(text));
  mc.fill(color);
  mc.drawEllipse(mc.circle(socketRadius, translateToPosition(socket)));
};

const drawConnections = (mc, translateToPosition, globalTranslate, inputPositions, rack, ctx) => ([inputKey, inputObj]) => {
  if (inputObj.type === 'connection') {
    const inputModule = rack.find(md => inputObj.module === getId(md));
    if (inputModule) {
      const inputProp = inputObj.property;
      const inputPos = inputPositions[inputKey].socket;
      const outputPos = vAdd(getPosition(inputModule), getOutputPositionsRaw(inputModule)[inputProp].socket);

      const p1 = vAdd(translateToPosition(inputPos), [-socketRadius, 0]);
      const p2 = vAdd(globalTranslate(outputPos), [socketRadius, 0]);
      const dist = Math.abs(p1[0] - p2[0]);
      const bxOffset = dist / 2;
      drawBezier(ctx, p1, vAdd(p1, [-bxOffset, 0]), vAdd(p2, [bxOffset, 0]), p2)
    }
  } else if (inputObj.type === 'value') {
    const inputPos = translateToPosition(inputPositions[inputKey].socket);
    mc.fill([0, 50, 200, 0.5]);
    mc.drawEllipse(mc.circle(socketRadius, inputPos));
    mc.fill([255, 255, 255, 0.5])
  }
}

export const drawRack = (rack, mc, ctx, globalTranslate, selectionData) => {
  mc.background([0,0,0,1]);
  mc.strokeWeight(2);
  mc.stroke([255, 255, 255, 1]);
  rack.forEach(md => {
    const translateToPosition = R.compose(globalTranslate, vAdd(getPosition(md)));
    Object.entries(getInputs(md))
      .forEach(drawConnections(mc, translateToPosition, globalTranslate, getInputPositionsRaw(md), rack, ctx));
  });

  rack.forEach(moduleDef => {
    const p = getPosition(moduleDef);
    const textPosition = getTextPosition(moduleDef);
    const inPosition = getInPosition(moduleDef);
    const outPosition = getOutPosition(moduleDef);
    const [dx, dy] = getDimensions(moduleDef);
    const inputPositions = getInputPositionsRaw(moduleDef);
    const outputPositions = getOutputPositionsRaw(moduleDef);
    const moduleName = getModuleName(moduleDef);
    const id = getId(moduleDef);

    const tagColor = getTagColor(findModule(moduleName).tag);
    const translateToPosition = R.compose(globalTranslate, vAdd(p));

    const modulePoints = [
      [0, 0],
      [dx, 0],
      [dx, dy],
      [0, dy]
    ].map(v => translateToPosition(v));

    const headerPoints = [
      [0, 0],
      [dx, 0],
      [dx, 30],
      [0, 30]
    ].map(v => translateToPosition(v));

    mc.stroke([255, 255, 255, 1]);

    mc.fill([0,0,0,1]);
    mc.drawPolygon({points: modulePoints});

    mc.fill(tagColor);
    mc.drawPolygon({points: headerPoints});

    mc.fill([255, 255, 255, 0.5]);

    // Module Title
    ctx.fillText(`${moduleName} (${id})`, ...translateToPosition(textPosition));

    // Title box line
    mc.fill([255, 255, 255, 0.25]);
    mc.drawLine(mc.line(
      translateToPosition([0, 30]),
      translateToPosition([dx, 30]),
    ));
    mc.fill([255, 255, 255, 0.5]);

    // In
    ctx.fillText('In', ...translateToPosition(inPosition));

    // Out
    ctx.fillText('Out', ...translateToPosition(outPosition));

    Object.entries(inputPositions).forEach(drawInputSockets(mc, ctx, moduleDef, translateToPosition, getInputs(moduleDef)));
    Object.entries(outputPositions).forEach(drawOutputSockets(mc, ctx, moduleDef, rack, translateToPosition));
  });

  if (selectionData.isInSelectionMode) {
    if (selectionData.isSelecting) {
      mc.fill([255, 255, 255, 0.1]);
      mc.stroke([255, 255, 255, 0.5]);
      const rect = regularRect(selectionData.selectionAreaStart, selectionData.selectionAreaEnd);
      mc.drawPolygon(mc.polygon(rect));
    }
  }

  if (selectionData.selectedModules.length > 0) {
    const so = 10;
    mc.strokeWeight(2);
    mc.stroke([255, 255, 255, 0.75]);
    mc.fill([0,0,0,0]);
    rack.forEach(md => {
      if (selectionData.selectedModules.includes(getId(md))) {
        const p1 = vAdd(getPosition(md), [-so/2, -so/2]);
        const p2 = vAddAll([p1, getDimensions(md), [so, so]]);
        const rect = regularRect(p1, p2).map(v => globalTranslate(v));
        mc.drawShape(mc.polygon(rect));
      }
    });
  }
}