import { getId, getInputsValues } from "../react/reducers/rack";

const checkDependants = (md, rack, seen) => {
  const dependants = rack.filter(md =>
    getInputsValues(md).some(input =>
      input.type === 'connection' && input.module === getId(md)
    )
  );

  const results = [];
  for (const dependantModule of dependants) {
    const id = getId(dependantModule);
    const seenBranch = [...seen, id];
    if (seen.includes(id)) {
      console.log(`detected cycle! ${seenBranch.join(' -> ')}`)
      return true;
    }
    results.push(checkDependants(dependantModule, rack, seenBranch));
  }

  return results.reduce((a, b) => a || b, false);
}

export const checkForCycles = rack => {
  for (const md of rack) {
    const seen = [];
    seen.push(getId(md));
    if (checkDependants(md, rack, seen)) {
      // found a cycle
      return true;
    }
  }
  // No cycles
  return false;
}
