import {defaultTo} from 'ramda';
import * as L from 'partial.lenses';
import {rack} from '../../rack/rack';

const root = L.prop('settings');
const resetTimeL = L.compose(root, L.prop('resetTime'));
const executionsPerFrameL = L.compose(root, L.prop('executionsPerFrame'));

const initialValue = {
  resetTime: rack.resetTime,
  executionsPerFrame: rack.executionsPerFrame
};

export default (state, action) => {
  switch (action.type) {
    case 'RESET_TIME_ON': return L.set(resetTimeL, true, state);
    case 'RESET_TIME_OFF': return L.set(resetTimeL, false, state);

    case 'SET_EXECUTIONS_PER_FRAME': return L.set(executionsPerFrameL, action.payload, state);

    default: return L.set(root, defaultTo(initialValue, L.get(root, state)), state);
  }
};

export const selectors = {
  resetTime: L.get(resetTimeL),
  executionsPerFrame: L.get(executionsPerFrameL)
};
