import {tagType} from '../../util/types';
import {vTowards} from 'vec-la-fp';

export const VectorLerp = {
  name: 'VectorLerp',
  tag: 'Vector',
  inputs: {
    v: {type: 'Vector', required: true},
    v2: {type: 'Vector', required: true},
    t: {type: 'Number', required: false}
  },
  outputs: {
    output: 'Vector'
  },
  fn: ({ v, v2, t }) => {
    // Note: This only implements the multiply=1 case and doesn't allow for variadic time
    const tag = tagType({v, v2});
    let out;

    switch (tag) {
      case 'v:v,v2:v': {
        out = vTowards(t, v, v2);
        break;
      }
      case 'v:v,v2:p': {
        out = v2.map(v2 => vTowards(t, v, v2));
        break;
      }
      case 'v:p,v2:v': {
        out = v.map(v => vTowards(t, v, v2));
        break;
      }
      case 'v:p,v2:p': {
        out = v.map(v => v2.map(v2 => vTowards(t, v, v2)));
        break;
      }
      case 'v:pa,v2:v': {
        out = v.map(v => v.map(v => vTowards(t, v, v2)));
        break;
      }
      case 'v:pa,v2:p': {
        out = v.map(v => v.map(v => v2.map(v2 => vTowards(t, v, v2))));
        break;
      }
      case 'v:pa,v2:pa': {
        out = v.map(v => v.map(v => v2.map(v2 => v2.map(v2 => vTowards(t, v, v2)))));
        break;
      }
      case 'v:v,v2:pa': {
        out = v2.map(v2 => v2.map(v2 => vTowards(t, v, v2)));
        break;
      }
      case 'v:p,v2:pa': {
        out = v2.map(v2 => v2.map(v2 => v.map(v => vTowards(t, v, v2))));
        break;
      }
    }

    return {
      output: out
    };
  }
};
