import { pointInCircle } from '../util/math-util';
import { socketRadius } from '../constants';
import {
  getPosition,
  getId,
  getInputPositions
} from '../react/reducers/rack';

export const mousedown = (props, clickPosition) => {
  const {
    rack,
    globalTranslate,
    setRawValue,
    isInRawMode,
    rawValue
  } = props;

  if (isInRawMode) {
    return rack.some(md => {
      getInputPositions(md).forEach(([inputKey, {socket}]) => {
        if (pointInCircle(globalTranslate(socket, getPosition(md)), socketRadius, clickPosition)) {
          const value = JSON.parse(rawValue);
          setRawValue(getId(md), inputKey, value);
          return true;
        }
      });
    });
  }
};
